body {
  background-color: #add8e6;
  font-family: Arial, sans-serif;
}

section {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.wrapper {
  gap: 16px;
  display: flex;
}

.leftPart {
  flex-direction: column;
  align-items: center;
  display: flex;
}

form {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
}

.game--container {
  width: 306px;
  grid-template-columns: repeat(3, auto);
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.cell {
  width: 100px;
  height: 100px;
  cursor: pointer;
  color: #8a2be2;
  border: 1px solid #333;
  font-family: Permanent Marker, cursive;
  font-size: 60px;
  line-height: 100px;
  box-shadow: 0 0 0 1px #333;
}

.action {
  color: #ff4500;
}

.oPlayerInput {
  color: #8a2be2;
}

.xPlayerInput {
  color: #ff4500;
}

button {
  text-transform: capitalize;
  color: #333;
  cursor: pointer;
  background-color: azure;
  border: none;
  border-radius: 16px;
  padding: 8px;
}

button:hover {
  transform: scale(1.1);
}

.x--statistic {
  text-transform: capitalize;
  color: #ff4500;
}

.o--statistic {
  text-transform: capitalize;
  color: #8a2be2;
}

